@import "variables";

.admonition {
    padding: 15px;
    margin-bottom: 21px;
    border-left: 10px solid transparent;
}

.admonition .title {
    margin: 0;
    text-transform: uppercase;
    padding-left: 3px;
    border: 1px solid;
    border-style: hidden hidden solid;
}

.admonition .content {
    padding-left: .75em;
    padding-right: .75em;
    padding-top: 0.5em;
    margin-left: 0;
    border-left: 0;
    border-top: 0;
    min-height: 0;
}

.note {
  border-color: olive;
  
  @media (prefers-color-scheme: dark) {
    background-color: $dark-background-secondary;
  }

  @media (prefers-color-scheme: light) {
    background-color: $light-background-secondary;
  }

  [data-theme=dark] & {
    background-color: $dark-background-secondary;
  }

  [data-theme=light] & {
    background-color: $light-background-secondary;
  }
}

.note .title {
    color: olive;
    border-color: olive;
}

.tip {
    border-color: blue;
  
    @media (prefers-color-scheme: dark) {
        background-color: $dark-background-secondary;
    }

    @media (prefers-color-scheme: light) {
        background-color: $light-background-secondary;
    }

    [data-theme=dark] & {
        background-color: $dark-background-secondary;
    }

    [data-theme=light] & {
        background-color: $light-background-secondary;
    }
}

.tip .title {
    color: blue;
    border-color: blue;
}

.warning {
    border-color: orange;
  
    @media (prefers-color-scheme: dark) {
        background-color: $dark-background-secondary;
    }

    @media (prefers-color-scheme: light) {
        background-color: $light-background-secondary;
    }

    [data-theme=dark] & {
        background-color: $dark-background-secondary;
    }

    [data-theme=light] & {
        background-color: $light-background-secondary;
    }
}

.warning .title {
    color: orange;
    border-color: orange;
}

.important {
    border-color: #bc4444;
 
    @media (prefers-color-scheme: dark) {
        background-color: $dark-background-secondary;
    }

    @media (prefers-color-scheme: light) {
        background-color: $light-background-secondary;
    }

    [data-theme=dark] & {
        background-color: $dark-background-secondary;
    }

    [data-theme=light] & {
        background-color: $light-background-secondary;
    }
}

.important .title {
    color: #bc4444;
    border-color: #bc4444;
}

